import { createCache, getValue } from '../../@glimmer/validator/index.js';

let caches = [];


export const subtle = {
  flushSync() {
    caches.forEach(cache => {
      getValue(cache);
    });
  },
  sync(fn) {
    let cache = createCache(fn);

    caches.push(cache);

    return () => {
      caches = caches.filter(c => c!==cache)
    }
  }
}
